<template>
	<div>
		<div
			@click="showTestData"
		>
			<table
				v-for="testData, index in testDataList"
				:key="testData.idTest"
			>
				<tr>
					<td>
						Тест номер {{ index + 1 }}
					</td>
				</tr>
        
        <th>Сессиия</th>
				<th>Пользователь</th>
				<th>Дата</th>
				<th>Время</th>
				<th>Порядковый номер вопроса</th>
				<th>ID вопроса</th>
				<th>Вопрос</th>
				<th>Выбранный вариант ответа</th>
				<th>Результат: Верно/не верно</th>

				<tr
					v-for="answer in testDataList[index].answers"
					:key="answer.id"
				>
          <td>{{answer.user_session_id}}</td>
					<td>{{answer.created_by}}</td>
					<td>{{getDateFromISO(answer.created_at)}}</td>
					<td>{{getTimeFromISO(answer.created_at)}}</td>
					<td>-</td>
					<td>{{answer.id}}</td>
					<td>{{answer.question}}</td>
					<td>{{answer.currentAnswer}}</td>
					<td>{{answer.isRight}}</td>
				</tr>
				

			</table>
		</div>
		
	</div>
</template>

<script>
import axios from 'axios'

export default{
    data() {
			return {
				token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE2NzcyMzI2NDMuNTcxNzE1LCJpYXQiOjE2NzYxNTI2NDMuNTcxNzE2LCJ1c2VybmFtZSI6ImFkbWluIiwicm9sZXMiOlsiYWRtaW4tcGFydCIsIm1hbmFnZS11c2VycyIsIm1hbmFnZS1yb2xlcyJdfQ.J_C7cQV4d3Geqt_JKdVG1ctQks5WNKg118hM7cOXRGA",
				testDataList: [
					{
						idTest: 2,
						answers: [],
						questions: []
					},
					{
						idTest: 3,
						answers: [],
						questions: []
					}
				]
			}
    },

    methods: {

			GET_TEST_QUESTIONS(itemDataList){
			axios({
				method: 'get',
				url: `https://dev1.med-game.ru/api/v1/admin/test/${itemDataList.idTest}/questions`,
				headers: { Authorization: `Bearer ${this.token}` }
			})
			.then((testQuestions) => {
				itemDataList.questions = testQuestions.data.data
				return testQuestions
			})
			.then((testQuestions) => {
				this.GET_TEST_ANSWERS(itemDataList, testQuestions.data.data)
			})
			.catch((error) => {
				console.log('Ошибка:');
				console.log(error)
				return error
			})
			},

			GET_TEST_ANSWERS(itemDataList, testQuestionsList){
			axios({
				method: 'get',
				url: `https://dev1.med-game.ru/api/v1/admin/test/${itemDataList.idTest}/answers`,
				headers: { Authorization: `Bearer ${this.token}` }
			})
			.then((testAnswers) => {
				// console.log(testAnswers.data.data);
				console.log(testQuestionsList);
				itemDataList.answers = testAnswers.data.data			
				
			})
			.then(() => {
				this.writeQuestionsForAnswer(itemDataList)
				
			})					
			.catch((error) => {
				console.log('Ошибка:');
				console.log(error)
				return error
			})
			},

			writeQuestionsForAnswer(itemDataList){
				// console.log(itemDataList);
				
				for (let i = 0; i < itemDataList.answers.length; i++) {
					
					const questionByAnswer = itemDataList.questions.find(x => x.id === itemDataList.answers[i].question_id)
					// console.log(questionByAnswer);

					if (questionByAnswer) {
						itemDataList.answers[i].question = questionByAnswer.body.text
						// console.log(questionByAnswer.question_type_id )
            
            if (questionByAnswer.question_type_id === 3) {
              itemDataList.answers[i].question = itemDataList.answers[i].body.description
            }


            const currentAnswer =  questionByAnswer.body.answers?.items.find(x => x.id === itemDataList.answers[i].body.description.answerId)
            if (currentAnswer) {
              itemDataList.answers[i].currentAnswer = currentAnswer.text
              itemDataList.answers[i].isRight = currentAnswer.isRight
            }
					}
				}
			},

			loadTestDataList(){
				for (let i = 0; i < this.testDataList.length; i++) {
					this.GET_TEST_QUESTIONS(this.testDataList[i])
					// this.GET_TEST_ANSWERS(this.testDataList[i])
				}
				
			},

      getDateFromISO(serverTime) {
        const d1 = new Date(serverTime)
        return d1.toLocaleDateString()
      },
      getTimeFromISO(serverTime) {
        const d1 = new Date(serverTime)
        return d1.toLocaleTimeString()
      },

			showTestData(){
				console.log(this.testDataList)
			}

    },
    mounted() {
			this.loadTestDataList()
    },
}
</script>

<style lang="scss">

  table{
    border-collapse: collapse;
    margin-bottom: 20px;
  }

  th{
    border: 1px solid black;
  }

  td{
    border: 1px solid black;
    padding: 2px;
  }
</style>
